import React from 'react';
import { useRef } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { CloseOutlined, LeftOutlined } from '@ant-design/icons';
import {
  setActiveHeaderMobileAction,
  setIsOpenHeaderSpMobileAction,
} from '../../store/Animation/animationSlice';

const HeaderSpMobile = ({ children }) => {
  const [maxHeight, setMaxHeight] = useState('0px');
  const dispatch = useDispatch();

  const { isOpenHeaderSpMb, activeHeaderMobile } = useSelector(
    (state) => state.animationSlice,
  );
  const contentRef = useRef(null);

  useEffect(() => {
    // o lng chiu cao ca phn t v cp nht maxHeight
    const updateMaxHeight = () => {
      const height = contentRef.current.scrollHeight; // Ly chiu cao thc t ca ni dung
      setMaxHeight(isOpenHeaderSpMb ? `${height}px` : '0px');
    };

    updateMaxHeight();
    // Cp nht li chiu cao khi isOpenHeaderSp thay i hoc khi window resize
    window.addEventListener('resize', updateMaxHeight);

    return () => {
      window.removeEventListener('resize', updateMaxHeight);
    };
  }, [isOpenHeaderSpMb, children]); // Ph thuc vo isOpenHeaderSp  bit khi no cp nht chiu cao
  const handleCloseHeader = () => {
    dispatch(setIsOpenHeaderSpMobileAction(false));
  };
  const handleBackMainHeader = () => {
    dispatch(setActiveHeaderMobileAction(0));
  };
  return (
    <div
      style={{
        maxHeight: isOpenHeaderSpMb ? '100%' : '0%',
      }}
      ref={contentRef}
      className="absolute w-full h-full  z-20 duration-200 bg-white overflow-hidden"
    >
      <div className="py-5 container_td flex justify-between color_text_content">
        {activeHeaderMobile ? (
          <LeftOutlined
            style={{ fontSize: '20px' }}
            onClick={handleBackMainHeader}
            className="font-bold"
          />
        ) : (
          <span></span>
        )}
        <CloseOutlined
          onClick={handleCloseHeader}
          style={{ fontSize: '20px' }}
        />
      </div>
      <div className="py-6">{children}</div>
    </div>
  );
};

export default HeaderSpMobile;
